header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  height: 120px;
  padding: 30px 100px;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
  display: flex !important;
  background-color: black;
}
.sideBar {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 100;
  background: linear-gradient(
    to right top,
    #060930,
    #141137,
    #23153e,
    #321944,
    #401d4a,
    #4d214f,
    #592453,
    #662856,
    #742d59,
    #83325b,
    #91385d,
    #9e3f5e
  );
  display: none;
  transition: 0.5s;
}

.sideBar .close {
  background: url(../assets/close.png);
  background-size: contain;
  width: 40px;
  height: 40px;
  margin: 20px;
}

.sideBar ul {
  position: absolute;

  top: 20%;
  width: 100%;
  text-decoration: none;
  list-style: none;
}

.sideBar ul li {
  padding: 20px;
  text-align: center;
  font-weight: 800;
}
.sideBar ul li a {
  width: 100%;
}

.sideBar ul li:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

header .logo {
  font-size: 2em;
  color: #fff;
  font-weight: 800;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 2px;
}

header .toggle {
  position: relative;
  width: 30px;
  height: 30px;
  background: url("../assets/menu2.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 30px;
  cursor: pointer;
  display: none;
}

header ul {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

header ul li {
  position: relative;
  list-style: none;
  margin-left: 40px;
}
header ul li a {
  color: white;
  text-decoration: none;
}
header ul li a:hover {
  color: white;
  text-decoration: none;
  font-weight: 800;
}
header a img {
  width: 10%;
  margin-right: 10px;
}

header a #xiom-logo {
  width: 15%;
}

@media (max-width: 768px) {
  header {
    padding: 10px 50px;
  }
  header a #xiom-logo {
    width: 40%;
  }
  header a img {
    width: 30%;
  }
}

@media (max-width: 425px) {
  header .toggle {
    display: block;
    width: 200%;
    height: 70px;
    background-size: 100%;
  }
  header ul {
    display: none;
  }
  header a img {
    width: 20%;
  }
}
